body {
	font-family: $gotham;
	font-style: normal;
	font-weight: 500;
	color: $navy;
	font-size: 1rem;
	// @include bp(phone) {
	// 	font-size: 0.7rem;
	// }
}
h1 {
	font-size: 60px;
	line-height: 1em;
	@include bp(phone) {
		font-size: 40px;
	}
	// @include bp(phone){
	// 	font-size: 35px;
	// }
}
h2 {
	font-size: 40px;
	line-height: 1.1em;
	// margin-bottom: 0.5em;
	@include bp(tablet) {
		font-size: 36px;
	}
	@include bp(phone) {
		font-size: 25px;
		margin-bottom: 0;
		line-height: 1em;
	}
}
h3 {
	font-size: 30px;
	line-height: 1.4em;
	@include bp(S) {
		font-size: 26px;
	}
}
h4 {
	font-size: 25px;
	font-weight: 400;
	line-height: 1.2em;
	@include bp(S) {
		font-size: 18px;
	}
}
h5 {
	font-size: 20px;
	font-weight: 400;
	line-height: 1.2em;
	@include bp(S) {
		font-size: 20px;
	}
}
.heading6 {
	font-size: 14px;
	@include bp(phone-tablet) {
		font-size: 13px;
	}
}
.smaller_h1 {
	font-size: 55px !important;
	@include bp(phone) {
		font-size: 45px !important;
	}
}
p {
	font-size: 15px;
	font-weight: 400;
	line-height: 1.7em;
	@include bp(phone) {
		font-size: 12px;
		line-height: 1.2em;
	}
	@include bp(tablet) {
		font-size: 14px;
		line-height: 1.4em;
	}
}
